<script>
import Cookies from 'js-cookie'
import { getmobieuserData, getuserData } from '@/api/app'

export default {
  data () {
    return {
      isActive: false,
      isDisplay: true,
      userList: [],
      isUnfold: false,
      // 左侧导航菜单
      isNavCosplay: false,
      isUnNavCosplay: true,
      isNavBizhi: false,
      isUnNavBizhi: true,
      isUnNavPhoto: true,
      isNavPhoto: false,
      isUnNavNews: true,
      isNavNews: false,
      isUnNavTx: true,
      isNavTx: false,
      // 搜索框
      isSearch: false,
      // 个人信息
      isUser: false,
      isDesc: false,
      mobieList: []
    }
  },
  async created () {
    this.getCookieLogin()

    const res1 = await getuserData()
    this.userList = res1

    const res2 = await getmobieuserData()
    this.mobieList = res2
  },
  mounted: function () {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll () {
      const h = document.documentElement.scrollTop || document.body.scrollTop

      if (h > 100) {
        this.isActive = true
        this.isDisplay = false
      } else {
        this.isActive = false
        this.isDisplay = true
      }
    },
    getCookieLogin () {
      const rememberSuc = Cookies.get('rememberSuc')
      const rememberFai = Cookies.get('rememberFai')
      this.$store.state.isLoginSuc = rememberSuc === undefined ? false : JSON.parse(rememberSuc)
      this.$store.state.isLoginFai = rememberFai === undefined ? true : JSON.parse(rememberFai)
    },
    signout () {
      this.$store.state.isLoginSuc = false
      this.$store.state.isLoginFai = true
      Cookies.remove('rememberSuc')
      Cookies.remove('rememberFai')
    },
    unfoldmenu () {
      this.isUnfold = true
    },
    // 左侧导航栏
    handleTouchStart (e) {
      this.touchStartX = e.touches[0].pageX
    },
    handleTouchLeft (e) {
      const touchCurrentX = e.touches[0].pageX
      if (touchCurrentX < this.touchStartX) {
        // 用户向左滑动
        this.isUnfold = false
      }
    },
    handleTouchRight (e) {
      const touchCurrentX = e.touches[0].pageX
      if (touchCurrentX > this.touchStartX) {
        // 用户向右滑动
        this.isUser = false
      }
    },
    handleTouchEnd (e) {
      // 清空触摸起始位置
      this.touchStartX = null
    },
    navcosplay () {
      this.isNavCosplay = !this.isNavCosplay
      this.isUnNavCosplay = !this.isUnNavCosplay
    },
    navbizhi () {
      this.isNavBizhi = !this.isNavBizhi
      this.isUnNavBizhi = !this.isUnNavBizhi
    },
    navphoto () {
      this.isNavPhoto = !this.isNavPhoto
      this.isUnNavPhoto = !this.isUnNavPhoto
    },
    navnews () {
      this.isNavNews = !this.isNavNews
      this.isUnNavNews = !this.isUnNavNews
    },
    navtx () {
      this.isNavTx = !this.isNavTx
      this.isUnNavTx = !this.isUnNavTx
    },
    // 搜索框
    searchunfold () {
      this.isSearch = !this.isSearch
    },
    // 个人信息
    usermenu () {
      this.isUser = true
    },
    desc () {
      this.isDesc = true
    },
    mobiesignout () {
      this.$store.state.isLoginSuc = false
      this.$store.state.isLoginFai = true
      Cookies.remove('rememberSuc')
      Cookies.remove('rememberFai')
      this.isUser = false
    }
  }
}
</script>
