<template>
    <div class="forgotpwd">
      <div class="forgot-right">
        <transition name="fade">
          <div v-show="isShow" :class="{'prompt': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">
            <i :class="{'iconfont': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">{{ promptIco }}</i>
            &nbsp;
            <span>{{ promptMain }}</span>
          </div>
        </transition>
        <div class="forgot-form">
          <form action="">
            <div class="forgot-account">
              <label for="acc">用户名:</label>
              <input type="text" name="acc" placeholder="用户名" v-model="user.username">
            </div>
            <div :class="{'kong20': true, 'hide': isAppear}"></div>
            <button type="button" :class="{'reset-btn': true, 'hide': isAppear}" @click="reset">重置密码</button>
            <div class="kong10"></div>
            <div :class="{ 'forgot-email': true, 'appear': isAppear}">
              <label for="email">邮箱:</label>
              <input type="text" name="email" placeholder="电子邮箱" v-model="user.email" @change="verifyEmail">
            </div>
            <div class="kong10"></div>
            <div :class="{'forgot-emailcode': true, 'appear': isAppear}">
              <label for="emailcode">邮箱验证码:</label>
              <input type="text" name="emailcode" placeholder="输入邮箱验证码" v-model="user.emailcode" @change="verifyEmailCode">
              <button type="button" @click="getCode" :disabled="dis">
                <span v-if="show">获取验证码</span>
                <span v-else>重新获取({{ count }}s)</span>
              </button>
            </div>
            <div class="kong10"></div>
            <div :class="{'forgot-pwd': true, 'appear': isAppear}">
              <label for="pwd">新密码:</label>
              <input type="password" name="pwd" placeholder="密码(6位以上)" v-model="user.password" @change="verifyPwd">
            </div>
            <div class="kong10"></div>
            <div :class="{'forgot-pwd': true, 'appear': isAppear}">
              <input type="password" name="pwd" placeholder="重复新密码" v-model="user.repassword" @change="verifyRePwd">
            </div>
            <div class="kong20"></div>
            <div :class="{'form-submit': true, 'appear': isAppear}">
              <button type="button" @click="confirm">确认修改</button>
              <router-link to="/login/loginhome">返回登录</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </template>

<script>
import { getloginData } from '@/api/login'
import axios from 'axios'
export default {
  name: 'forgotpwdIndex',
  data () {
    return {
      user: {
        username: '',
        password: '',
        repassword: '',
        email: '',
        emailcode: ''
      },
      dis: false,
      show: true,
      timer: null,
      count: '',
      loginList: [],
      isAppear: false,
      resetId: '',
      // 提示框部分
      promptMain: '',
      isRed: false,
      isGreen: false,
      isShow: false,
      promptIco: ''
    }
  },
  async created () {
    const res1 = await getloginData()
    this.loginList = res1
  },
  methods: {
    reset () {
      if (this.user.username === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '用户名不能为空'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      for (let i = 0; i < this.loginList.length; i++) {
        if (this.user.username === this.loginList[i].username) {
          this.resetId = i
          this.isAppear = true
          return false
        }
      }
      this.isRed = true
      this.promptIco = ''
      this.promptMain = '该用户名未注册'
      this.isShow = true
      setTimeout(() => {
        this.isShow = false
      }, 2000)
      return false
    },
    getCode () {
      if (this.user.username === '' || this.user.email === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '用户名和邮箱不能为空'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
      } else {
        const TIME_COUNT = 60
        if (!this.timer) {
          this.count = TIME_COUNT
          this.show = false
          this.dis = true
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--
            } else {
              this.dis = false
              this.show = true
              clearInterval(this.timer)
              this.timer = null
            }
          }, 1000)
        }
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '发送验证码功能作者还未完成，请使用默认验证码: 1234'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 6000)
      }
    },
    verifyEmail () {
      const regemail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (!regemail.test(this.user.email)) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '邮箱格式不合法'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      if (this.user.email === this.loginList[this.resetId].email) {
        return false
      }
      this.isRed = true
      this.promptIco = ''
      this.promptMain = '邮箱和用户名不匹配'
      this.isShow = true
      setTimeout(() => {
        this.isShow = false
      }, 2000)
      return false
    },
    verifyEmailCode () {
      if (this.user.emailcode !== '1234') {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '验证码错误'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    verifyPwd () {
      if (this.user.password.length < 6) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '密码长度过短'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    verifyRePwd () {
      if (this.user.repassword !== this.user.password) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '两次密码不一致'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    confirm () {
      if (this.user.password === this.loginList[this.resetId].password) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '新密码与原密码相同'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      const userId = this.loginList[this.resetId].id
      const newData = {
        nameid: this.resetId,
        username: this.loginList[this.resetId].username,
        email: this.loginList[this.resetId].email,
        password: this.user.password
      }
      // axios.put(`http://localhost:3000/user/${userId}`, newData)
      axios.put(`https://my-json-server.typicode.com/sptzz/jsonserver/user/${userId}`, newData)
      this.isGreen = true
      this.promptIco = ''
      this.promptMain = '修改密码成功'
      this.isShow = true
      setTimeout(() => {
        this.isShow = false
      }, 2000)
      this.user.username = ''
      this.user.email = ''
      this.user.emailcode = ''
      this.user.password = ''
      this.user.repassword = ''
    }
  }
}
</script>

<style lang="less" scoped>
  .forgot-right {
    width: 50%;
    float: left;
    .forgot-form {
      margin: 0 auto 20px;
      max-width: 400px;
      .forgot-account,
      .forgot-email,
      .forgot-emailcode,
      .forgot-pwd {
        label {
          font-size: 15px;
          line-height: 30px;
        }
        input {
          display: block;
          width: 100%;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          color: #555;
          border-top-width: 0;
          border-left-width: 0;
          border-right-width: 0;
          border-bottom-width: 1px;
          border-bottom-color: #b9b9b9;
          padding: 6px 12px 6px 10px;
        }
        input::placeholder {
          color: #b5b5b5;
        }
      }
      .forgot-emailcode {
        position: relative;
        input {
            width: 65%;
        }
        button {
            position: absolute;
            bottom: 0;
            right: 0;
            width: 32%;
            height: 34px;
            line-height: 34px;
            background-color: #333;
            color: #fff;
            text-align: center;
            border-radius: 4px;
            border: none;
            cursor: pointer;
        }
      }
      .form-submit {
        height: 40px;
        margin-bottom: 30px;
        button {
          display: block;
          float: left;
          width: 45%;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          color: #fff;
          background-color: #56b9cb;
          border-radius: 5px;
          letter-spacing: 5px;
          border: none;
          cursor: pointer;
        }
        button:hover {
          background-color: #549ca9;
        }
        a {
          display: block;
          float: right;
          width: 45%;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          color: #676767;
          text-align: center;
          border: 1px #666 solid;
          border-radius: 5px;
        }
      }
      .forgot-email,
      .forgot-emailcode,
      .forgot-pwd,
      .form-submit {
        display: none;
      }
      .appear {
        display: block;
      }
      .hide {
        display: none!important;
      }
      .reset-btn {
        display: block;
        width: 100%;
        height: 50px;
        max-width: 400px;
        font-size: 16px;
        color: #fff;
        background: linear-gradient(90deg, #ea2074 10%, #f93d3f 90%);
        border: none;
        border-radius: 25px;
        letter-spacing: 5px;
        cursor: pointer;
      }
    }
  }
  @media screen and (max-width: 800px) {
  .forgot-right {
    width: 100%;
  }
}

  .kong10 {
    height: 10px;
  }
  .kong20 {
    height: 20px;
  }
  .fade-enter-active, .fade-leave-active {
      transition: opacity .8s;
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
    .prompt {
      max-width: 400px;
      height: 50px;
      border-radius: 5px;
      margin: 0 auto;
      padding: 0 5px;
      font-size: 14px;
      line-height: 50px;
      i {
        font-family: icomoon;
        font-size: 16px;
        vertical-align: bottom;
      }
    }
    .prompt-show-red {
      background-color: #ff7875;
      color: #5c0011;
    }
    .prompt-show-green {
      background-color: #95de64;
      color: #092b00;
    }
</style>
