<template>
  <div class="registered">
    <div class="reg-right">
      <div class="reg-body">
        <transition name="fade">
          <div v-show="isShow" :class="{'prompt': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">
            <i :class="{'iconfont': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">{{ promptIco }}</i>
            &nbsp;
            <span>{{ promptMain }}</span>
          </div>
        </transition>
        <div class="reg-form">
          <form action="">
            <div class="reg-account">
              <label for="acc">用户名:</label>
              <input type="text" name="acc" placeholder="请输入英文,数字用户名,不要输入中文" v-model="user.username" @change="verifyName">
            </div>
            <div class="kong10"></div>
            <div class="reg-email">
              <label for="email">邮箱:</label>
              <input type="text" name="email" placeholder="电子邮箱" v-model="user.email" @change="verifyEmail">
            </div>
            <div class="kong10"></div>
            <div class="reg-emailcode">
              <label for="emailcode">邮箱验证码:</label>
              <input type="text" name="emailcode" placeholder="输入邮箱验证码" v-model="user.emailcode" @change="verifyEmailCode">
              <button type="button" @click="getCode" :disabled="dis">
                <span v-if="show">获取验证码</span>
                <span v-else>重新获取({{ count }}s)</span>
              </button>
            </div>
            <div class="kong10"></div>
            <div class="reg-pwd">
              <label for="pwd">密码:</label>
              <input type="password" name="pwd" placeholder="密码(6位以上)" v-model="user.password" @change="verifyPwd">
            </div>
            <div class="kong10"></div>
            <div class="reg-pwd">
              <input type="password" name="pwd" placeholder="重复密码" v-model="user.repassword" @change="verifyRePwd">
            </div>
            <div class="kong20"></div>
            <div class="form-submit">
              <button type="button" @click="register">注册</button>
              <router-link to="/login/loginhome">返回登录</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { getloginData } from '@/api/login'
export default {
  name: 'registeredIndex',
  data () {
    return {
      user: {
        username: '',
        password: '',
        repassword: '',
        email: '',
        emailcode: ''
      },
      dis: false,
      show: true,
      timer: null,
      count: '',
      loginList: [],
      // 提示框部分
      promptMain: '',
      isRed: false,
      isGreen: false,
      isShow: false,
      promptIco: ''
    }
  },
  async created () {
    const res1 = await getloginData()
    this.loginList = res1
  },
  methods: {
    getCode () {
      if (this.user.username === '' || this.user.email === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '用户名和邮箱不能为空'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
      } else {
        const TIME_COUNT = 60
        if (!this.timer) {
          this.count = TIME_COUNT
          this.show = false
          this.dis = true
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--
            } else {
              this.dis = false
              this.show = true
              clearInterval(this.timer)
              this.timer = null
            }
          }, 1000)
        }
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '发送验证码功能作者还未完成，请使用默认验证码: 1234'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 6000)
      }
    },
    verifyName () {
      const regname = /^[a-zA-Z0-9]{3,10}$/
      if (!regname.test(this.user.username)) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '用户名不合法'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      for (let i = 0; i < this.loginList.length; i++) {
        if (this.user.username === this.loginList[i].username) {
          this.isRed = true
          this.promptIco = ''
          this.promptMain = '用户名已被注册'
          this.isShow = true
          setTimeout(() => {
            this.isShow = false
          }, 2000)
          return false
        }
      }
      return true
    },
    verifyEmail () {
      const regemail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (!regemail.test(this.user.email)) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '邮箱格式不合法'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      for (let i = 0; i < this.loginList.length; i++) {
        if (this.user.email === this.loginList[i].email) {
          this.isRed = true
          this.promptIco = ''
          this.promptMain = '邮箱已被注册'
          this.isShow = true
          setTimeout(() => {
            this.isShow = false
          }, 2000)
          return false
        }
      }
      return true
    },
    verifyEmailCode () {
      if (this.user.emailcode !== '1234') {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '验证码错误'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    verifyPwd () {
      if (this.user.password.length < 6) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '密码长度过短'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    verifyRePwd () {
      if (this.user.repassword !== this.user.password) {
        this.isRed = true
        this.promptIco = ''
        this.promptMain = '两次密码不一致'
        this.isShow = true
        setTimeout(() => {
          this.isShow = false
        }, 2000)
        return false
      }
      return true
    },
    register () {
      const data = { nameid: this.loginList.length, username: this.user.username, email: this.user.email, password: this.user.password }
      // axios.post('http://localhost:3000/user', data)
      axios.post('https://my-json-server.typicode.com/sptzz/jsonserver/user', data)
      this.isGreen = true
      this.promptIco = ''
      this.promptMain = '注册成功'
      this.isShow = true
      setTimeout(() => {
        this.isShow = false
      }, 2000)
      this.user.username = ''
      this.user.email = ''
      this.user.emailcode = ''
      this.user.password = ''
      this.user.repassword = ''
    }
  }
}
</script>

<style lang="less" scoped>
.reg-right {
    width: 50%;
    float: left;
  .reg-body {
    .reg-form {
        margin: 0 auto 20px;
        max-width: 400px;
      .reg-account,
      .reg-email,
      .reg-pwd,
      .reg-emailcode {
        max-width: 400px;
        margin-top: 5px;
        margin-bottom: 8px;
        label {
          font-size: 15px;
          line-height: 30px;
        }
        input {
          display: block;
          width: 100%;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          color: #555;
          border-top-width: 0;
          border-left-width: 0;
          border-right-width: 0;
          border-bottom-width: 1px;
          border-bottom-color: #b9b9b9;
          padding: 6px 12px 6px 10px;
        }
        input::placeholder {
          color: #b5b5b5;
        }
      }
      .reg-emailcode {
        position: relative;
        input {
            width: 65%;
        }
        button {
            position: absolute;
            bottom: 0;
            right: 0;
            width: 32%;
            height: 34px;
            line-height: 34px;
            background-color: #333;
            color: #fff;
            text-align: center;
            border-radius: 4px;
            border: none;
            cursor: pointer;
        }
      }
      .form-submit {
      height: 40px;
      margin-bottom: 30px;
      button {
        display: block;
        float: left;
        width: 45%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #fff;
        background-color: #56b9cb;
        border-radius: 5px;
        letter-spacing: 5px;
        border: none;
        cursor: pointer;
      }
      button:hover {
        background-color: #549ca9;
      }
      a {
        display: block;
        float: right;
        width: 45%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #676767;
        text-align: center;
        border: 1px #666 solid;
        border-radius: 5px;
      }
    }
      .kong10 {
        height: 10px;
      }
      .kong20 {
        height: 20px;
      }
    }
    .fade-enter-active, .fade-leave-active {
      transition: opacity .8s;
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
    }
    .prompt {
      max-width: 400px;
      height: 50px;
      border-radius: 5px;
      margin: 0 auto;
      padding: 0 5px;
      font-size: 14px;
      line-height: 50px;
      i {
        font-family: icomoon;
        font-size: 16px;
        vertical-align: bottom;
      }
    }
    .prompt-show-red {
      background-color: #ff7875;
      color: #5c0011;
    }
    .prompt-show-green {
      background-color: #95de64;
      color: #092b00;
    }
  }
}
@media screen and (max-width: 800px) {
  .reg-right {
    width: 100%;
  }
}
</style>
