<template>
    <div class="loginhome">
          <!-- 登录部分 -->
      <div class="login-right">
        <transition name="fade">
          <div v-show="isShow" :class="{'prompt': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">
            <i :class="{'iconfont': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">{{ promptIco }}</i>
            &nbsp;
            <span>{{ promptUser }}</span>
            <strong>{{ capUser }}</strong>
            <span>{{ promptMain }}</span>
          </div>
        </transition>
        <div class="form-box">
          <form action="">
            <div class="account">
              <label for="acc">用户名:</label>
              <input type="text" id="icc" placeholder="用户名/邮箱" v-model="user.username">
            </div>
            <div class="kong10"></div>
            <div class="password">
              <label for="pass">密码:</label>
              <input type="password" id="pass" placeholder="密码" v-model="user.password">
            </div>
            <div class="captcha">
              <label for="cap">验证码:</label>
              <input type="text" id="cap" placeholder="右侧图形验证码中的数字" v-model="user.code">
              <div class="cap-img">
                <ValidCode @update:value="getCode"></ValidCode>
              </div>
            </div>
            <div class="kong10"></div>
            <div style="height: auto; overflow: hidden; max-width: 400px;">
              <div style="float: left;">
                <input type="checkbox" v-model="isChecked">&nbsp;记住我
                <span style="color: #bdbaba; padding-left: 10px; font-size: 12px;">公用电脑请勿勾选</span>
              </div>
              <div style="float: right;">
                <router-link to="/login/forgotpwd" class="forgot-pass">忘记密码</router-link>
              </div>
            </div>
            <div class="kong20"></div>
            <div class="form-submit">
              <button type="button" @click="login" >登录</button>
              <router-link to="/login/register">注册</router-link>
            </div>
          </form>
        </div>
        <div class="other-login">
          <div class="third">—— 使用第三方登录 ——</div>
          <div class="other-login-main">
            <a href="#" class="qq">
              <i class="iconfont"></i>
              &nbsp;
              QQ登录
            </a>
            <a href="#" class="weibo">
              <i class="iconfont"></i>
              &nbsp;
              微博登录
            </a>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import ValidCode from '@/components/ValidCode.vue'
import { getloginData } from '@/api/login'
import router from '@/router'
import Cookies from 'js-cookie'

export default {
  name: 'loginhomeIndex',
  data () {
    return {
      code: '', // 验证码组件传递过来的code
      user: {
        code: '', // 表单里用户输入的code
        username: '',
        password: ''
      },
      loginList: [],
      isUsername: false,
      userId: '',
      isPassword: false,
      isChecked: false,
      // 提示框部分
      promptMain: '',
      promptUser: '',
      capUser: '',
      isRed: false,
      isGreen: false,
      isShow: false,
      promptIco: ''
    }
  },
  async created () {
    const res1 = await getloginData()
    this.loginList = res1

    this.getCookie()
  },
  components: {
    ValidCode
  },
  methods: {
    getCode (code) {
      this.code = code
    },
    getUser () {
      for (let i = 0; i < this.loginList.length; i++) {
        if (this.loginList[i].username === this.user.username || this.loginList[i].email === this.user.username) {
          this.isUsername = true
          this.userId = this.loginList[i].nameid
        }
      }
    },
    getPass () {
      this.getUser()
      if (this.isUsername) {
        if (this.loginList[this.userId].password === this.user.password) {
          this.isPassword = true
        }
      }
    },
    login () {
      this.getUser()
      this.getPass()
      this.capUser = this.user.username
      if (this.user.username === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入用户名或邮箱'
      } else if (this.user.password === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入密码'
      } else if (this.user.code === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入验证码'
      } else {
        if (this.isUsername) {
          if (this.isPassword) {
            if (this.code.toLowerCase === this.user.code.toLowerCase) {
              this.isGreen = true
              this.promptIco = ''
              this.capUser = ''
              this.promptMain = '登录成功'
              if (this.isChecked) {
                Cookies.set('username', this.user.username, { expires: 30 })
                Cookies.set('password', this.user.password, { expires: 30 })
                Cookies.set('rememberMe', this.isChecked, { expires: 30 })
              } else {
                Cookies.remove('username')
                Cookies.remove('password')
                Cookies.remove('rememberMe')
              }
              this.$store.state.isLoginSuc = true
              this.$store.state.isLoginFai = false
              if (this.$store.state.isLoginSuc) {
                Cookies.set('rememberSuc', this.$store.state.isLoginSuc, { expires: 30 })
                Cookies.set('rememberFai', this.$store.state.isLoginFai, { expires: 30 })
              } else {
                Cookies.remove('rememberSuc')
                Cookies.remove('rememberFai')
              }
              router.push('/')
            } else {
              this.isRed = true
              this.promptIco = ''
              this.promptUser = ''
              this.capUser = ''
              this.promptMain = '验证码错误'
            }
          } else {
            this.isRed = true
            this.promptIco = ''
            this.promptUser = '为用户名'
            this.promptMain = '指定的密码不正确,请重新输入'
          }
        } else {
          this.isRed = true
          this.promptIco = ''
          this.promptUser = '用户名'
          this.promptMain = '未在本站点注册。请重新输入'
        }
      }
      this.user.code = ''
      this.isShow = true

      setTimeout(() => {
        this.isShow = false
      }, 2000)
    },
    getCookie () {
      const username = Cookies.get('username')
      const password = Cookies.get('password')
      const rememberMe = Cookies.get('rememberMe')

      this.user.username = username === undefined ? this.user.username : username
      this.user.password = password === undefined ? this.user.password : password
      this.isChecked = rememberMe === undefined ? false : JSON.parse(rememberMe)
    }
  }
}
</script>

<style lang="less" scoped>
  // 登录部分
.login-right {
  float: left;
  width: 50%;
  .form-box {
    max-width: 400px;
    margin: 0 auto;
    .account, .password {
      max-width: 400px;
      margin-top: 5px;
      margin-bottom: 8px;
      label {
        font-size: 15px;
        line-height: 30px;
      }
      input {
        display: block;
        width: 100%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #555;
        border-top-width: 0;
        border-left-width: 0;
        border-right-width: 0;
        border-bottom-width: 1px;
        border-bottom-color: #b9b9b9;
        padding: 6px 12px 6px 10px;
      }
      input::placeholder {
        color: #b5b5b5;
      }
    }
    .captcha {
      position: relative;
      max-width: 400px;
      margin-top: 5px;
      margin-bottom: 8px;
      label {
        font-size: 15px;
        line-height: 30px;
      }
      input {
        display: block;
        width: 55%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #555;
        border-top-width: 0;
        border-left-width: 0;
        border-right-width: 0;
        border-bottom-width: 1px;
        border-bottom-color: #b9b9b9;
        padding: 6px 12px 6px 10px;
      }
      input::placeholder {
        color: #b5b5b5;
      }
      .cap-img {
        position: absolute;
        top: 10px;
        left: 240px;
        display: block;
        width: 40%;
        height: 60px;
      }
    }
    .kong10 {
      height: 10px;
    }
    .kong20 {
      height: 20px;
    }
    .form-submit {
      height: 40px;
      margin-bottom: 30px;
      button {
        display: block;
        float: left;
        width: 45%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #fff;
        background-color: #56b9cb;
        border-radius: 5px;
        letter-spacing: 5px;
        border: none;
        cursor: pointer;
      }
      button:hover {
        background-color: #549ca9;
      }
      a {
        display: block;
        float: right;
        width: 45%;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        color: #676767;
        text-align: center;
        border: 1px #666 solid;
        border-radius: 5px;
      }
    }
    .forgot-pass {
      color: #a8a8a8;
      transition: all .3s;
    }
    .forgot-pass:hover {
      color: #549ca9;
    }
  }
  .prompt {
    max-width: 400px;
    height: 50px;
    border-radius: 5px;
    margin: 0 auto;
    padding: 0 5px;
    font-size: 14px;
    line-height: 50px;
    i {
      font-family: icomoon;
      font-size: 16px;
      vertical-align: bottom;
    }
  }
  .prompt-show-red {
    background-color: #ff7875;
    color: #5c0011;
  }
  .prompt-show-green {
    background-color: #95de64;
    color: #092b00;
  }
  .other-login {
      padding: 0 0 30px;
      margin: 20px auto;
      border-top: none;
      max-width: 400px;
      .third {
        font-size: 14px;
        color: #afafaf;
        height: 40px;
        line-height: 40px;
        text-align: center;
      }
      .other-login-main {
        height: 40px;
        margin-top: 20px;
        margin-bottom: 30px;
        text-align: center;
        a {
          position: relative;
          color: #545454;
          font-size: 14px;
          text-align: center;
          padding: 15px 40px;
          margin: 0 5px;
          border-radius: 3px;
        }
        .qq {
          border: 1px #2196f3 solid;
          i {
            font-family: icomoon;
            font-size: 24px;
            color: #2196F3;
            vertical-align: bottom;
          }
        }
        .weibo {
          border: 1px #f7470d solid;
          i {
            font-family: icomoon;
            font-size: 24px;
            color: #f7470d;
            vertical-align: bottom;
          }
        }
      }
    }
}
@media screen and (max-width: 800px) {
  .login-right {
    width: 100%;
  }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .8s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
